module.exports = {
	imageBaseSrc: '/static/',

	//apih
	hostname() {
		// return "http://192.168.43.93:8877"
		// return "http://101.33.210.213:8877"
		// return ""
		return "https://www.lib.gold"
	},

	//api
	baseurl() {
		var url = this.$hostname();
		return url + '';
	},

	header: {
		'content-type': 'application/x-www-form-urlencoded'
	},

	//api,fun()datasuccfuncitonapit
	http(fun, data, succ, header, t) {
		if (t) {
			uni.showLoading({
				"title": "",
				"mask": true
			});
		}
		var _this = this;
		uni.request({
			"url": this.$baseurl() + fun,
			"method": "POST",
			"data": data,
			"header": header ? header : this.$header,
			"success": function(res) {
				uni.stopPullDownRefresh();
				if (t) {
					uni.hideLoading();
				}
				var result = res.data;
				succ(result);
			},
			"complete": function(res) {

			},
			"fail": function(res) {
				uni.showToast({
					title: "",
					icon: 'none'
				})
			}
		})
	},
	//api,fun()datasuccfuncitonapit
	getHttp(fun, data, succ, header, t) {
		if (t) {
			uni.showLoading({
				"title": "",
				"mask": true
			});
		}
		var _this = this;
		uni.request({
			"url": this.$baseurl() + fun,
			"method": "GET",
			"data": data,
			"header": header ? header : this.$header,
			"success": function(res) {
				uni.stopPullDownRefresh();
				if (t) {
					uni.hideLoading();
				}
				var result = res.data;
				succ(result);
			},
			"complete": function(res) {

			},
			"fail": function(res) {
				uni.showToast({
					title: "",
					icon: 'none'
				})
			}
		})
	},
	//
	showToast(title, fun) {
		uni.showToast({
			title: title,
			icon: 'none'
		})
		if (fun) {
			setTimeout(function() {
				fun();
			}, 2000);
		}
	},

	//
	//1: 2 3 4 5. 6. 7.Logo
	upLoadFile(url, filePath, data, succ) {
		console.log("this.hostname()", this.$hostname() + url)
		console.log("filePath",filePath)
		wx.uploadFile({
			url: this.$hostname() + url, //
			filePath, //tempFilePaths[0]
			name: 'file',
			formData: {
				...data
			},
			success: succ,
			fail: err => {
				console.log(err,'qqqq')
			},
		});
	},

	//
	chooseImage() {
		return wx.chooseImage({
			count: 1,
			sizeType: ["original", "compressed"],
			sourceType: ["album", "camera"],
			type: 'image'
		});

	},
	
	//
	chooseFile() {
		return wx.chooseMessageFile({
		  count: 1,
		  type: 'file',
		})
	},
	
	downFile(url,succ){
		wx.downloadFile({
		  url: url,
		  success:succ
		})
	}
}
